@import "../css/1-normalize.css"; /* ---- Main Variables ---- */
/* Colors */
:root {
  --main-bg-color: #fff;
  --light-bg-color: #efefef;
  --dark-bg-color: #222;
  --main-font-color: #212121;
  --minor-font-color: #eee;
  --dark-overlay: rgba(33,33,33,0.7);
  --light-overlay: rgba(239,239,239,0.4);
  --minor-color: #ca4b20;
  --minor-color-filter: invert(35%) sepia(29%) saturate(2942%)
                          hue-rotate(346deg) brightness(93%) contrast(93%);
}

.dark-theme {
  --main-bg-color: #444;
  --light-bg-color: #757575;
  --main-font-color: #fff;
}

/* Media Query Breakpoints */
/* ---- Helpers ---- */
/* Media Query Breakpoints */
/* ---- Base Rules ---- */
/* Reset */
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  scroll-behavior: smooth;
}

img,
svg {
  max-width: 100%;
  vertical-align: middle;
}

svg {
  max-height: 100%;
}

figure {
  margin: 0;
  text-align: center;
  overflow: hidden;
}

ol,
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

/* Custom */
body {
  background-color: var(--main-bg-color);
  color: var(--main-font-color);
  font-family: "Source Sans Pro", sans-serif;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.6;
}

h1,
h2,
h3,
h4 {
  text-transform: capitalize;
}

p {
  font-family: "Open Sans", sans-serif;
}

a:link,
a:visited,
a:hover,
a:active {
  vertical-align: middle;
  color: var(--main-font-color);
  text-decoration: none;
  transition: color 0.5s;
}

a:hover,
a:active {
  color: var(--minor-color);
}

a.skip-link {
  position: absolute;
  left: 50%;
  padding: 4px 8px;
  background: var(--minor-color);
  color: #fff;
  text-align: center;
  transform: translate(-50%, -100%);
  transition: transform 0.5s;
}
a.skip-link:focus {
  transform: translate(-50%, 0%);
}

.top-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  visibility: hidden;
  position: fixed;
  bottom: 20px;
  right: 20px;
  width: 35px;
  height: 35px;
  background-color: var(--minor-color);
  opacity: 0;
  border: none;
  border-radius: 4px;
  transition: all 0.5s;
  cursor: pointer;
  z-index: 100;
}
.top-btn.show {
  visibility: visible;
  opacity: 0.8;
}
.top-btn span {
  margin-top: 4px;
  padding: 4px;
  border: solid #fff;
  border-width: 0 2px 2px 0;
  transform: rotate(-135deg);
}
.top-btn:hover, .top-btn:focus {
  opacity: 1;
}

/* ---- Layout Rules ---- */
.site-wrap {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  grid-template-rows: auto;
  grid-template-areas: "hd" "main" "ft";
  margin-left: auto;
  margin-right: auto;
}

main {
  grid-area: main;
}

.container {
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding: 5rem 1rem;
}
@media (min-width: 768px) {
  .container {
    max-width: 750px;
  }
}
@media (min-width: small) {
  .container {
    max-width: 750px;
  }
}
@media (min-width: 992px) {
  .container {
    max-width: 970px;
  }
}
@media (min-width: medium) {
  .container {
    max-width: 970px;
  }
}
@media (min-width: 1200px) {
  .container {
    max-width: 1170px;
  }
}

.flex--row {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  row-gap: 3rem;
}

.flex--col {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

/* ---- Module Rules ---- */
/* Buttons */
.main--btn {
  padding: 0.5rem 4rem;
  background: linear-gradient(to right, transparent 50%, var(--main-font-color) 50%) no-repeat;
  background-size: 200% 100% !important;
  color: var(--main-font-color);
  font-weight: 600;
  border: 2px solid var(--main-font-color);
  border-radius: 0.2rem;
  transition: color 1s, background 1s !important;
  cursor: pointer;
}
.main--btn:hover, .main--btn:focus {
  color: var(--main-bg-color);
  background-position: right;
}

/* ---- State Rules ---- */
.hide {
  display: none !important;
}

.show {
  visibility: visible !important;
  opacity: 1 !important;
}

.sr--only:not(:focus) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0, 0, 0, 0);
  overflow: hidden;
}

/* ---- Modifiers Rules ---- */
.text--center {
  text-align: center;
}

.disable--scrolling {
  height: 100%;
  overflow: hidden;
  touch-action: none;
}

/* ---- Header ---- */
.site-header {
  grid-area: hd;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background: transparent;
  text-transform: uppercase;
  transition: background 0.5s, box-shadow 0.5s;
  z-index: 100;
}
.site-header.change--bg {
  background: var(--main-bg-color);
  box-shadow: 0 3px 5px 0.3px rgba(0, 0, 0, 0.1);
}
.site-header .container {
  padding-top: 0;
  padding-bottom: 0;
}
.site-header .header__logo h1, .site-header .header__logo h2 {
  font-size: 1.25rem;
}
.site-header nav ul {
  display: none;
}
.site-header nav ul li {
  display: inline-block;
  margin-right: 1rem;
  padding: 0.5rem;
}
.site-header nav ul li a {
  display: block;
  font-weight: bold;
}
@media (min-width: 992px) {
  .site-header nav ul {
    display: block;
  }
}
@media (min-width: medium) {
  .site-header nav ul {
    display: block;
  }
}
.site-header nav .toggle-menu {
  display: inline-block;
  background: transparent;
  border: 0;
  cursor: pointer;
}
.site-header nav .toggle-menu span {
  display: block;
  width: 1.5rem;
  height: 3px;
  margin: 4px auto;
  background: var(--main-font-color);
  transition: background 0.3s ease-out, transform 0.3s linear;
}
.site-header nav .toggle-menu:hover span {
  background: var(--minor-color);
}
.site-header nav .toggle-menu.open span:first-child {
  transform: translateY(7px) rotate3d(0, 0, 1, 45deg);
}
.site-header nav .toggle-menu.open span:nth-child(2) {
  background-color: transparent !important;
}
.site-header nav .toggle-menu.open span:last-child {
  transform: translateY(-7px) rotate3d(0, 0, 1, -45deg);
}

/* ---- Fullscreen Menu ---- */
.fullscreen-menu {
  position: fixed;
  visibility: hidden;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: var(--main-bg-color);
  opacity: 0;
  overflow: auto;
  transition: 0.5s ease-in-out;
  z-index: 99;
}
.fullscreen-menu ul li.lang {
  font-family: "Almarai", sans-serif;
}
.fullscreen-menu ul li a {
  font-size: 2rem;
  font-weight: bold;
  text-transform: uppercase;
}
@media (min-width: 992px) {
  .fullscreen-menu ul li a {
    font-size: 3rem;
  }
}
@media (min-width: medium) {
  .fullscreen-menu ul li a {
    font-size: 3rem;
  }
}
.fullscreen-menu .theme {
  margin-top: 2rem;
}
.fullscreen-menu .theme .theme-btn {
  position: absolute;
  opacity: 0;
  width: 0;
  height: 0;
}
.fullscreen-menu .theme .toggle-theme {
  position: relative;
  width: 80px;
  height: 40px;
  padding: 7px;
  background: var(--dark-bg-color);
  border-radius: 40px;
  cursor: pointer;
}
.fullscreen-menu .theme .toggle-theme svg {
  width: 25px;
  height: 25px;
  transition: fill 0.3s;
  z-index: 1;
}
.fullscreen-menu .theme .toggle-theme svg:hover {
  fill: var(--minor-color);
}
.fullscreen-menu .theme .toggle-theme .sun {
  fill: var(--main-font-color);
}
.fullscreen-menu .theme .toggle-theme .moon {
  fill: var(--main-bg-color);
}
.fullscreen-menu .theme .toggle-theme .switch-slider {
  position: absolute;
  top: 5px;
  left: 5px;
  width: 30px;
  height: 30px;
  background: #fff;
  border-radius: 50%;
  transition: transform 0.5s;
}
.fullscreen-menu .theme .theme-btn:focus + .toggle-theme {
  box-shadow: 0 -5px 3px -3px var(--minor-color), 0 5px 3px -3px var(--minor-color);
}
.fullscreen-menu .theme .theme-btn:checked + .toggle-theme .switch-slider {
  transform: translateX(40px);
}
@media (min-width: 992px) {
  .fullscreen-menu .theme {
    margin-top: 3rem;
  }
}
@media (min-width: medium) {
  .fullscreen-menu .theme {
    margin-top: 3rem;
  }
}

/* ---- Start 404 Page ---- */
.error .error-num {
  display: block;
  font-size: 10rem;
  font-weight: 900;
}
.error p {
  margin-bottom: 4rem;
}

/* ---- Start Footer ---- */
.site-footer {
  grid-area: ft;
  color: var(--minor-font-color);
  background: var(--dark-bg-color);
}
.site-footer h2, .site-footer h3 {
  margin-top: 0;
  text-transform: uppercase;
}
.site-footer a:not(:hover, :active) {
  color: var(--minor-font-color);
}
.site-footer .footer__widgets,
.site-footer .links {
  justify-content: flex-start;
  align-items: flex-start;
}
.site-footer .container {
  padding-bottom: 2rem;
}
.site-footer .contact {
  width: 100%;
}
.site-footer .contact form {
  margin-top: 2rem;
}
.site-footer .contact .flex--row {
  row-gap: initial;
}
.site-footer .contact label {
  display: block;
  position: relative;
  width: 100%;
  margin-bottom: 2rem;
  padding: 0.125rem 0;
  text-transform: uppercase;
  border-bottom: 1px solid var(--minor-font-color);
}
.site-footer .contact .placeholder {
  position: absolute;
  top: 50%;
  pointer-events: none;
  transform-origin: 0 50%;
  transform: translateY(-50%);
  transition: transform 0.3s, color 0.3s;
}
.site-footer .contact input,
.site-footer .contact textarea {
  display: block;
  width: 100%;
  padding: 0.125rem 0;
  color: var(--minor-font-color);
  background: transparent;
  border: 0;
  outline: 0;
  caret-color: var(--minor-font-color);
}
.site-footer .contact input::placeholder {
  color: transparent;
}
.site-footer .contact input:focus + .placeholder,
.site-footer .contact input:not(:placeholder-shown) + .placeholder {
  transform: translateY(calc(-100% - 0.25rem)) scale(0.75);
}
.site-footer .contact button {
  display: block;
  margin: 1rem 0;
  padding: 0;
  color: var(--minor-font-color);
  background: transparent;
  text-transform: uppercase;
  border: 0;
  border-bottom: 1px solid transparent;
  transition: color 0.5s, border 0.5s;
  cursor: pointer;
}
.site-footer .contact button:hover {
  color: var(--minor-color);
  border-bottom-color: var(--minor-color);
}
.site-footer .contact button:hover .arrow {
  border-left: 5px solid var(--minor-color);
}
.site-footer .contact .arrow {
  display: inline-block;
  width: 0;
  height: 0;
  border-top: 5px solid transparent;
  border-bottom: 5px solid transparent;
  border-left: 5px solid var(--minor-font-color);
  transition: border 0.5s;
}
.site-footer .links {
  width: 100%;
  margin-bottom: 2rem;
}
.site-footer .links div {
  width: 33.3333333333%;
}
.site-footer .copyright {
  justify-content: center;
  padding-top: 2rem;
  border-top: 1px solid var(--minor-font-color);
}
.site-footer .copyright .footer__menu {
  justify-content: center;
  gap: 2rem;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .site-footer .contact .flex--row {
    gap: 1.5rem;
    flex-wrap: nowrap;
  }
}
@media (min-width: small) {
  .site-footer .contact .flex--row {
    gap: 1.5rem;
    flex-wrap: nowrap;
  }
}
@media (min-width: 992px) {
  .site-footer .contact {
    width: 50%;
  }
  .site-footer .contact .contact__wrap {
    width: 80%;
  }
  .site-footer .links {
    width: 50%;
  }
  .site-footer .copyright {
    justify-content: space-between;
    text-align: initial;
  }
  .site-footer .copyright p {
    width: 50%;
  }
}
@media (min-width: medium) {
  .site-footer .contact {
    width: 50%;
  }
  .site-footer .contact .contact__wrap {
    width: 80%;
  }
  .site-footer .links {
    width: 50%;
  }
  .site-footer .copyright {
    justify-content: space-between;
    text-align: initial;
  }
  .site-footer .copyright p {
    width: 50%;
  }
}

/* ---- Arabic RTL ---- */
html[dir=rtl] body {
  font-family: "Almarai", sans-serif;
}
html[dir=rtl] .top-btn {
  right: auto;
  left: 20px;
}
html[dir=rtl] .main--btn {
  background: linear-gradient(to right, var(--main-font-color) 50%, transparent 50%) no-repeat;
  background-position: right;
}
html[dir=rtl] .main--btn:hover, html[dir=rtl] .main--btn:focus {
  background-position: left;
}
html[dir=rtl] .site-header nav .toggle-menu {
  margin-right: 1rem;
}
html[dir=rtl] .fullscreen-menu ul li.lang {
  font-family: "Source Sans Pro", sans-serif;
}
html[dir=rtl] .fullscreen-menu .theme .toggle-theme {
  flex-direction: row-reverse;
}
html[dir=rtl] .splide:not(.portfolio__slider) .splide__arrow.splide__arrow--prev {
  left: auto;
  right: 0;
}
html[dir=rtl] .splide:not(.portfolio__slider) .splide__arrow.splide__arrow--next {
  right: auto;
  left: 0;
}
html[dir=rtl] .hero::before {
  transform: scaleX(-1);
}
@media (min-width: 855px) {
  html[dir=rtl] .portfolio .projects .portfolio__slider .splide__track--rtl {
    padding-right: 0 !important;
    padding-left: 10rem !important;
  }
}
html[dir=rtl] .blog .previous-posts .blog__slider .pre-post {
  border-left: 0;
  border-right: 5px solid var(--main-font-color);
}
html[dir=rtl] .site-footer .contact .placeholder {
  transform-origin: right 50%;
}
html[dir=rtl] .site-footer .contact button:hover .arrow {
  border-right: 5px solid var(--minor-color);
}
html[dir=rtl] .site-footer .contact .arrow {
  border-left: 0;
  border-right: 5px solid var(--minor-font-color);
}

/* ---- Dark Theme ---- */
.dark-theme .splide__arrow svg {
  fill: var(--main-font-color);
}
.dark-theme .site-header.change--bg {
  background: var(--dark-bg-color);
}
.dark-theme .hero::before {
  background-image: linear-gradient(var(--dark-overlay), var(--dark-overlay)), url("../images/hero-576w.webp");
}
@media (min-width: 576px) and (max-width: 992px) {
  .dark-theme .hero::before {
    background-image: linear-gradient(var(--dark-overlay), var(--dark-overlay)), url("../images/hero-1080w.webp");
  }
}
@media (min-width: 992px) {
  .dark-theme .hero::before {
    background-image: linear-gradient(var(--dark-overlay), var(--dark-overlay)), url("../images/hero-1500w.webp");
  }
}
@media (min-width: medium) {
  .dark-theme .hero::before {
    background-image: linear-gradient(var(--dark-overlay), var(--dark-overlay)), url("../images/hero-1500w.webp");
  }
}
@media (min-width: 1200px) {
  .dark-theme .hero::before {
    background-image: linear-gradient(var(--dark-overlay), var(--dark-overlay)), url("../images/hero-2000w.webp");
  }
}
.dark-theme .intro img,
.dark-theme .services img,
.dark-theme .team img,
.dark-theme .social-icon img {
  filter: invert(1);
}